___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-size: 14px;
}

hr {
  margin: 0;
  padding: 0;
}

input {
  border: none;
  outline: none;
}

.add-button,
.checkbox-input,
footer {
  cursor: pointer;
}

body {
  background-color: lightgray;
  width: 420px;
  margin: 0 auto;
}

.main {
  margin-top: 20px;
  background-color: #fff;
  box-shadow: 10px 10px 10px 0 rgba(0, 0, 0, 0.3);
}

header,
.add-list-container,
.task,
footer {
  height: 50px;
  display: flex;
  align-items: center;
  color: #545862;
  font-size: 15px;
  font-weight: 400;
  padding: 1rem;
  line-height: 20px;
  margin: 0;
  width: 100%;
  text-overflow: ellipsis;
  overflow: hidden;
}

.add-task {
  width: 100%;
}

.add-button {
  width: 18px;
}

.text-input {
  width: 85%;
}

.task {
  display: flex;
  justify-content: space-between;
}

footer {
  display: flex;
  justify-content: center;
  background-color: rgba(153, 153, 153, 0.3803921569);
}

input[placeholder] {
  font-style: italic;
}

/* DOM MANIPULATION */
.drag-to-order {
  width: 10px;
  cursor: move;
